import React, { Component } from 'react';
import { Button, Modal,Tree, Input, message } from 'antd';
import styles from './index.module.less';
import { getAction, postAction } from '@/axios';
import {hasButtonPermission} from '@/util'
const { confirm } = Modal;
const { TreeNode } = Tree;

export default class LeftFolder extends Component {
    
    state = {
        treeData: [
            {
                title: '',
                id: '000',
                children:[]
            }
        ],
        expandedKeys: ['000'], // keys
        autoExpandParent: true, // 
        selectedKeys: [], // keys
        selectedInfo: '', //
        editModal: false, // /
        addModal: false, // /
        docName: '', // / 
    };
    componentDidMount(){
        this.getTreeData();
    }
    componentDidUpdate(preProps){
        if(preProps.OutFolderId!==this.props.OutFolderId){
            this.setState({
                selectedKeys:[this.props.OutFolderId]
            })
        }
    }
    //
    getTreeData = () => {
        getAction("/ybt/wf/process/queryFolderTree").then(res => {
            if(res.success){
                this.setState({
                    treeData:[
                        {
                            title: '',
                            id: '000',
                            children:res.obj || []
                        }
                    ]
                })
            }else{
                message.error(res.obj)
            }
        })
    }
    // 
    deleteHandle = () => {
        if(this.state.selectedKeys[0]==="000"){
            message.warn("");
            return false;
        }
        if(!this.state.selectedKeys[0]){ // 
            Modal.warning({
                title: '',
                content: '',
            });
        } else {
            // let selectedInfoChildren = this.state.selectedInfo.node.getNodeChildren()
            // if (selectedInfoChildren[0]) { // 
            //     confirm({
            //         title: '',
            //         content: ' ',
            //         okText: '',
            //         okType: 'danger',
            //         cancelText: '',
            //         onOk:()=> {
            //             this.deleteFolderRequest(this.state.selectedKeys[0])
            //         }
            //     });
            // } else { // 
            //     confirm({
            //         title: '',
            //         content: '',
            //         okText: '',
            //         okType: 'danger',
            //         cancelText: '',
            //         onOk:()=> {
            //             this.deleteFolderRequest(this.state.selectedKeys[0])
            //         }
            //     });
            // }
            confirm({
                title: '?',
                okText: '',
                okType: 'danger',
                cancelText: '',
                onOk:()=> {
                    this.deleteFolderRequest(this.state.selectedKeys[0])
                }
            });
        }
    }
    //----
    deleteFolderRequest = (key) => {
        getAction(`/ybt/wf/process/deleteFolderById?processFolderId=${key}`).then(res => {
            if(res.success){
                message.success("");
                this.getTreeData();
                this.setState({
                    selectedKeys:[]
                })
            }else{
                message.error(res.obj)
            }
        })
    }
    onExpand = expandedKeys => {
    // if not set autoExpandParent to false, if children expanded, parent can not collapse.
    // or, you can remove all expanded children keys.
    this.setState({
        expandedKeys,
        autoExpandParent: false,
    });
    };

    // 
    onSelect = (selectedKeys, selectedInfo) => {
        if(selectedKeys.length===0){
            return false;
        }
        //
        if(selectedKeys[0]===this.state.selectedKeys[0]){
            return false;
        }
        this.setState({ selectedKeys, selectedInfo});
        this.props.updateSelectFolder(selectedKeys[0]==="000"?"":selectedKeys[0]);
    };
    //
    renderTreeNodes = data => data.map(item => {
        if (item.children) {
        return (
            <TreeNode title={item.title} key={item.id}>
            {this.renderTreeNodes(item.children)}
            </TreeNode>
        );
        }
        return <TreeNode key={item.id} {...item} />;
    });    
    //  
    editHandle = () => {
        if(this.state.selectedKeys[0]==="000"){
            message.warn("");
            return false
        }
        if(!this.state.selectedKeys[0]){ // 
            Modal.warning({
                title: '',
                content: '',
            });
        } else {
            this.setState({
                editModal: true,
                docName:this.state.selectedInfo.node.props.title
            })
        }
    }
    // 
    addHandle = () => {
        const firstLevel = this.state.treeData[0].children.map(item => item.id);
        if(this.state.selectedKeys.length===0){
            this.setState({
                addModal: true,
                docName:'' // 
            })
        }else{
            if(firstLevel.indexOf(this.state.selectedKeys[0]) === -1 && this.state.selectedKeys[0] !=="000"){
                message.warn("");
                return false;
            }
            this.setState({
                addModal: true,
                docName:'' // 
            })
        }
    }
    // 
    hideEdit = () => {
        this.setState({ editModal: false })
    }
    // 
    hideAdd = () => {
        this.setState({ addModal: false })
    }

    // 
    editHandleOk = () => {
        this.hideEdit() // 
        const params = {
            processFolderId:this.state.selectedKeys[0],
            folderName:this.state.docName
        }
        postAction("ybt//wf/process/updateFolderById",params).then(res => {
            if(res.success){
                this.getTreeData();
            }else{
                message.error(res.obj)
            }
        })
    }

    // 
    addHandleOk = () => {
        // , id
        const params = {
            folderName:this.state.docName,
            delFlag:0,
            // parentFolderId:this.state.selectedKeys[0]==="000"?"":this.state.selectedKeys[0]
        }
        if(this.state.selectedKeys[0]!=="000"){
            params.parentFolderId = this.state.selectedKeys[0]
        }
        postAction("/ybt/wf/process/createFolder",params).then(res=> {
            if(res.success){
                message.success("");
                this.hideAdd() // 
                this.getTreeData();
            }else{
                message.error(res.obj)
            }
        })
       
    }

    // /
    docNameChange = (e) => {
        this.setState({
            docName: e.target.value
        })
    }

    render() {
        return (
            <div className={styles.LeftContainer}>

                <div className={styles.btns}>
                    {
                        hasButtonPermission('workFlow001') &&
                    <Button type='primary' onClick={this.addHandle} size="small"></Button>

                    }
                    {
                        hasButtonPermission('workFlow002') &&
                        <Button style={{margin: "0px 10px"}} onClick={this.editHandle} size="small"></Button>

                    }
                    {
                        hasButtonPermission('workFlow003') &&
                        <Button onClick={this.deleteHandle} size="small"></Button>

                    }
                </div>

                <div className={styles.folderTree}>
                    <Tree
                        onExpand={this.onExpand}
                        defaultExpandedKeys={["000"]}
                        expandedKeys={this.state.expandedKeys}
                        autoExpandParent={this.state.autoExpandParent}
                        onSelect={this.onSelect}
                        selectedKeys={this.state.selectedKeys}
                    >
                        {this.renderTreeNodes(this.state.treeData)}
                    </Tree>
                </div>

                <Modal
                    title=""
                    visible={this.state.editModal}
                    onOk={this.editHandleOk}
                    onCancel={this.hideEdit}
                    okText = ''
                    cancelText = ''
                    >
                    <p><Input value={this.state.docName} onChange={this.docNameChange} placeholder='' /></p>
                </Modal>

                <Modal
                    title=""
                    visible={this.state.addModal}
                    onOk={this.addHandleOk}
                    onCancel={this.hideAdd}
                    okText = ''
                    cancelText = ''
                    >
                    <p><Input value={this.state.docName} onChange={this.docNameChange} placeholder='' /></p>
                </Modal>

            </div>
        )
    }
}
